@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
/* base styles */

* {
    margin: 0;
    font-family: "Quicksand";
    color: #333;
}

.navbar {
    padding: 20px;
    display: flex;
    align-items: center;
    max-width: 600px;
    margin: 0 auto;
    border-bottom: 2px solid #555;
}

.navbar h1 {
    color: #1c519b;
}

.navbar .links {
    margin-left: auto;
}

.navbar a {
    margin-left: 30px;
    text-decoration: none;
    padding: 6px;
    font-size: larger;
    font-weight: bold;
}

.navbar a:hover {
    color: #1c519b;
}

.content {
    max-width: 600px;
    margin: 40px auto;
    padding: 20px;
}


/* blog previews / list */

.blog-preview {
    padding: 10px 16px;
    margin: 10px 0;
    border-bottom: 10px solid #fafafa;
}

.blog-preview:hover {
    box-shadow: 5px 5px 5px 5px rgba(0, 0, 0, 0.1);
}

.blog-preview h2 {
    font-size: 20px;
    color: #1c519b;
    margin-bottom: 8px;
}

.blog-preview a {
    text-decoration: none;
}

.blog-details p {
    color: #1c519b;
    font-style: italic;
}

.blog-details div {
    font-size: small;
}

.create {
    max-width: 400px;
    margin: 0 auto;
    text-align: center;
}

.create label {
    text-align: left;
    display: block;
}

.create h2 {
    font-size: 20px;
    color: #1c519b;
    margin-bottom: 30px;
}

.create input,
.create textarea,
.create select {
    width: 100%;
    padding: 6px 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    box-sizing: border-box;
    display: block;
}

.create button {
    background: #1c519b;
    color: #fff;
    border: 0;
    padding: 8px;
    border-radius: 8px;
}

.create button:hover {
    background: #03214a;
    color: #fff;
    border: 0;
    padding: 8px;
    border-radius: 8px;
}

.Notfound a {
    text-decoration: none;
    background: #1c519b;
    color: #fff;
    border: 0;
    padding: 10px;
    border-radius: 8px;
    width: 100px;
}

